<template>
    <div class="wrapper">
        <el-button type="primary" @click="open"></el-button>
    </div>
</template>

<script>
    export default {
        name: "SeniorCheck",
        methods: {
            open() {
                this.$alert('', '', {
                    confirmButtonText: ''
                });
            }
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .wrapper {
        .el-button {
            margin-top: 50px;
            margin-left: 100px;
        }
    }
</style>



